.s-intro {
  background-color: var(--color-white);
}

.s-intro__wrapper {
  padding: 0;
}

.s-intro__content {
  display: flex;
  align-items: center;
  min-height: 32rem;
  background-size: cover;
  background-position: right center;
}

.s-intro__info {
  max-width: 46.875rem;
}

.s-intro__title {
  margin-bottom: 1.875rem;
}

.s-intro__title h1 {
  margin-bottom: 0.625rem;
}

.s-environment__title {
  margin: 0;
}

.s-environment__title span {
  font-family: 'Didona', sans-serif;
  font-size: 1.75rem;
  font-weight: 400;
  line-height: 2.25rem;
  text-transform: uppercase;
}

.s-environment__inner {
  display: grid;
  gap: 1.25rem;
  grid-template-columns: repeat(1, 1fr);
}

.s-environment__content {
  height: 100%;
  padding: 1rem;
}

.s-environment__col--info {
  display: grid;
  background: var(--color-middle-crayola);
  border-radius: 0.625rem;
  place-content: center;
}

.s-environment__photos {
  display: grid;
  gap: 0.75rem;
  grid-template-columns: repeat(2, 1fr);
}

.s-environment__photo img {
  width: 100%;
  height: 100%;
  min-width: 0;
  border-radius: 0.625rem;
  object-fit: cover;
}

.s-author-program__list {
  display: grid;
  max-width: 66.875rem;
  margin: 0 auto;
  gap: 1.25rem;
  grid-template-columns: repeat(2, 1fr);
}

.s-author-program__info {
  padding: 0.75rem;
  background: var(--color-white);
  border-radius: 1.25rem;
  box-shadow: 0 0.0625rem 0.9375rem rgba(34, 34, 34, 0.04);
}

.s-author-program__info-title {
  margin-bottom: 0.625rem;
  text-transform: uppercase;
}

.s-author-program__card {
  overflow: hidden;
  border-radius: 1.25rem;
}

.s-author-program__card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.s-one-day {
  background-color: var(--color-middle-sand);
}

.s-one-day__list {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.25rem;
}

.s-one-day__list .m-info-card::before {
  position: absolute;
  height: 0.5625rem;
  content: '';
  background-color: var(--color-dark);
}

.s-one-day__list .m-info-card::after {
  position: absolute;
  height: 0.5625rem;
  content: '';
  background-color: var(--color-dark);
}

