.s-one-day__button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 1.875rem;
  background: var(--color-dark);
  border-radius: 0.625rem;
  box-shadow: 0 0.0625rem 0.9375rem rgba(34, 34, 34, 0.03);
}

.s-one-day__button::before {
  position: absolute;
  top: 1.125rem;
  right: 0;
  width: 6.75rem;
  height: 0.625rem;
  content: '';
  background-color: var(--color-white);
}

.s-one-day__button::after {
  position: absolute;
  bottom: 1.3125rem;
  left: 1.375rem;
  width: 7.875rem;
  height: 0.375rem;
  content: '';
  background-color: var(--color-white);
}

.s-one-day__button-label {
  text-transform: uppercase;
  color: var(--color-white);
}

